import React, {Component} from 'react';

export default class List extends Component{
  constructor(props){
    super(props);
    this.state = {
      newName: ""
    };
    this.getPrevName = "";
  }

  // componentDidMount(){
  //   console.log("List componentDidMount", this.props)
  // }

  componentWillReceiveProps(newProps){
    console.log("List componentWillReceiveProps", newProps)
    this.setState({
      newName: newProps.sendData
    })
  }

  shouldComponentUpdate(newProps, newState){
    return true;
  }

  componentWillUpdate(newProps, newState){
    console.log("componentWillUpdate", newProps, newState)
    this.getPrevName = newProps.sendData;
  }

  componentDidUpdate(prevProps, prevState){
    console.log("componentDidUpdate", prevProps, prevState)
    this.getPrevName = prevProps.sendData;
  }



  render(){
    console.log("@@@@@", this.getPrevName)
    return(
      <div>
        This is List Component
        <h1>{this.state.newName}</h1>
      </div>
    );
  }
}
